<template>
    <div>
        <div class="crumbs">
            <el-breadcrumb separator="/">
                <el-breadcrumb-item>
                    <i class="el-icon-lx-calendar"></i> 课程设计
                </el-breadcrumb-item>
            </el-breadcrumb>
        </div>
        <div class="container">
            <div class="form">
                <div class="form-top">
                    <el-radio-group size="small" v-model="choosed">
                      <el-radio-button label="未完成课程设计教学"></el-radio-button>
                      <el-radio-button label="已完成课程设计教学"></el-radio-button>
                    </el-radio-group>
                    <el-button class="fr" type="primary" @click="openLink" size="small">新建课程设计教学</el-button>
                </div>
                <div class="cont">
                    <div class="cont-one" v-for="item in items">
                        <div class="one-left">
                            <h2>2019级第一次课程设计</h2>
                            <p class="stu-num">参加学生：37人</p>
                            <p class="work-time">作业提交：2019-10-15 12:00 之前</p>
                            <p class="status">当前状态：教学中</p>
                        </div>
                        <div class="one-right">
                            <p><el-button type="primary" size="mini" @click="inLink" plain>进入教学</el-button></p>
                            <p><el-button type="primary" size="mini" @click="openLink" plain>修改教学</el-button></p>
                        </div>
                    </div>

                    <div class="pagination">
                        <el-pagination
                          background
                          layout="prev, pager, next"
                          :total="1000">
                        </el-pagination>
                    </div>
                    
                </div>
            </div>
        </div>
    </div>
</template>

<script>
export default {
    name: 'course',
    data() {
        return {
            choosed:'未完成课程设计教学',
            items:[1,1,1]
        };
    },
    methods: {
        openLink(){
            this.$router.push({path:'newcourse'})
        },
        inLink(){
            this.$router.push({path:'stuworklist'})
        }
    }
};
</script>
<style lang='scss' scoped>
    .cont-one{
        display:flex;
        padding:15px 20px;
        border:1px solid #ccc;
        margin-top:20px;
        .one-left{
            flex:1;
            font-size:14px;
            line-height:24px;
            h2{
                font-size:16px;
                line-height:28px;
            }
        }
        .one-right{
            width:90px;
            padding-top:20px;
            p{
                margin-top:10px;
            }
            button{
                text-align: right;
                display:block;
                margin-left:10px;
            }
        }
    }
    .pagination{
        margin-top:20px;
    }
</style>


